import React, { Component } from 'react'
// import Logo from '../../asset/srLogo.png'
import {Row, Col,Image} from 'antd'
import {getKhmerDate} from '../../function/fn'
import Logo from '../../asset/qr-srlogo.png'
import GoGlobal from '../../asset/goglobalschool.png'

export default class QrCodePrint extends Component {
    render() {
        let data = this.props.data
        let qrSrc = this.props.qrSrc

        console.log(data)

        return (
            <Row
                style={{
                    padding: 10,
                    textAlign:'center',
                    backgroundColor:'#DDE7F1',
                    height:'100%'
                }}
            >
                <Col
                    xs={24}
                >
                    <Image style={{width:150}} src={Logo} preview={false} />

                    <h2 className="HeaderFont" style={{fontSize:18,color:'#0A2BB1'}}></h2>
                    <h2 className="HeaderFont" style={{fontSize:18,color:'#0A2BB1'}}>{data?.name}</h2>

                    <Image style={{width:350}} src={qrSrc} preview={false} /><br/><br/>

                    <h2 className="HeaderFont" style={{fontSize:18,color:'#0A2BB1'}}></h2><br/>
                    <h4 style={{fontSize:14,color:'#0A2BB1'}}></h4>
                    <Image style={{width:70}} src={Logo} preview={false} />
                    <Image style={{width:70}} src={GoGlobal} preview={false} />
                    
                </Col>
            </Row>
        )
    }
}
